.card{
    border-radius: 50%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    overflow: hidden;
    background: rgba(210, 105, 30, 0.9);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.card p{
    opacity: 0.7;
    font-weight: 700;
    font-size: 0.8rem;
}
.card h2{
    font-size: 1rem;
    color: white;
}
.card img{
    background: chocolate;
    height: 100%;
    object-fit: contain;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.08);
}
.card:hover{
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}